// ResultCard.jsx
import React from 'react';
import {
  CardContainer,
  CardSection,
  LoanAmount,
  AmountLabel,
  Amount,
  AmountNote,
  MonthlyPayment,
  CTASection,
  CTAPrompt,
  CTAText,
  CTAButton,
  InvalidAmount
} from './ResultCard.styles';
import { formatCurrency } from '../../utils/calculator';

const ResultCard = ({ loanAmount, monthlyPayment, isValid }) => {
  return (
    <CardContainer>
      <CardSection>
        <LoanAmount>
          <AmountLabel>S tin vay</AmountLabel>
          {isValid ? (
            <Amount>${formatCurrency(loanAmount, 2)}</Amount>
          ) : (
            <InvalidAmount></InvalidAmount>
          )}
   
        </LoanAmount>
      </CardSection>

      <CardSection>
        <MonthlyPayment>
          <AmountLabel>S tin phi tr hng thng</AmountLabel>
          {isValid ? (
            monthlyPayment !== null ? (
              <Amount>${formatCurrency(monthlyPayment, 2)}</Amount>
            ) : (
              <InvalidAmount></InvalidAmount>
            )
          ) : (
            <InvalidAmount></InvalidAmount>
          )}
       
        </MonthlyPayment>
      </CardSection>

      <CTASection>
        <CTAPrompt>Sn sng mua tr gp?</CTAPrompt>
        <CTAText>Mua th mua</CTAText>
        <CTAButton>Nhn bo gi</CTAButton>
      </CTASection>
    </CardContainer>
  );
};

export default ResultCard;